.about {
  width: 100%;
  position: relative;
  background-color: #fff;
  padding-top: 40px;
  padding-bottom: 40px;
  color: #032B2F;

  h1 {
    font-size: 1.6rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 40px;
  }
  .main {
    width: 100%;
    max-width: 980px;
    margin: 0 auto;
  }
  .about_header {
    width: 100%;

    img {
      width: 150px;
      margin: 0 auto;
      display: block;
    }
  }
  .about_text {
    width: 100%;
    text-align: left;
    padding: 40px 20px 20px 30px;
    font-size: 0.9rem;
    font-weight: 500;
  }
}


@media screen and (min-width: 640px) {
  .about {
    .main {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 440px;

      .about_text {
        font-size: 1.2rem;
      }
    }
  }
}
